.boletos {
  padding: 5rem 0;
}
.boletos h3 {
  text-align: center;
  color: #ffffff;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase {
  color: black;
}
.pase .nombre-pase {
  text-align: center;
  font-size: 3rem;
  color: #ffffff;
}
.pase-header, .pase-footer {
  padding: 0.5rem;
}
.pase-header p, .pase-footer p {
  text-align: center;
  color: #ffffff;
  margin: 0;
}
.pase-header {
  background-color: #f53756;
}
.pase-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pase-body {
  background-color: #ffffff;
}
.pase-body ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.pase-body ul li,
.pase-body ul :nth-child(odd) {
  background-color: #ffffff;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase-footer {
  background-color: #752f97;
}
.pase-footer p {
  font-size: 2.4rem;
  font-weight: 700;
}